import { Component, OnInit } from '@angular/core';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Ticket } from '../../domain/Conjunto/Ticket';
import { TicketService } from '../../service/Tickets/ticket.service';
import {DataTableModule,SharedModule, Message, DataTable, SelectItem, ProgressBarModule} from 'primeng/primeng';
import { SignalR, SignalRConnection, IConnectionOptions } from 'ng2-signalr';
import * as urls from '../../domain/Shared/global';


@Component({
  selector: 'app-atc',
  templateUrl: './atc.component.html',
  styleUrls: ['./atc.component.css'],
  providers: [TicketService]
})

export class AtcComponent implements OnInit {
  paqtickets: Ticket[];
  paqtickets2: Ticket[];
  exx: String;
  ele: Ticket; 
  Id: string;
  Fe: string;
  Ho: string;
  TT: string;
  me: string; 
  es: boolean;
  pare : number;
  ti: string ;
  ser: string;
  value: number;
  ide: number;
  mesa: number;
  datos:String;
  serv: SelectItem[];
  msgs: Message[];
  selected: string;
  min: number;
  seg: number;
  hor: number;
  xxx: number;
  yyy: number;
  d1: boolean;
  d2: boolean;
  d3: boolean;
  estado:boolean;
  xxxx: boolean;
  display: boolean;
  private _connection: SignalRConnection;
  
  o: IConnectionOptions = {
    hubName: 'HammerHub',
    qs: { name: 'mesa' + Math.random(), group: 'Ticket' },
    url: urls.urlSockets
  };


  constructor(private http: Http,
    private ticketService: TicketService,
    private _signalR: SignalR
  ) {
      
        this.serv=[];
        this.serv.push({label:'Acreditacion', value: 'Acreditacion'} )
        this.serv.push({label:'Delegacion', value: 'Delegacion'} )
        this.serv.push({label:'Prensa', value: 'Prensa'} )
        this.serv.push({label:'Tecnica', value: 'Tecnica'} )
        this.serv.push({label:'Otros', value: 'Otros'} )
        
        this.me="";
        this.value=0;
        this.mesa= 1;
        this.min=0;
        this.hor=0;
        this.seg=0;
        this.xxx=0;
        this.display=false;
  }
  obtener() {
    this.pare = 1;
    this.ticketService
      .TodoTicket()
      .then(res => {
        
      if (res!=null)
        { this.paqtickets = res; 
            this.ele = this.paqtickets[0];
            this.paqtickets2=this.paqtickets;
            
            this.Id=this.ele.Id;
            this.Fe=this.ele.fecha;
            this.Ho= this.ele.hora;
            this.es = this.ele.estado;
            this.TT = this.ele.tkt;  
            this.ti= "00:00:00";
            this.ser= "-";
            this.me="Mesa "+this.mesa;
            this.value=0;
            this.xxx=0; 
            
         
              if (this.Id!="")
                {    
                    this.ide= parseInt(this.Id)
                    this.ticketService.llamadoticket (this.ide).subscribe(res => {
                        const resp = res.json();
                        if (resp > 0) {
                            console.log(" ya da llamado Ticket:"+ this.Id);
                            console.log(this.ele);
                                  }
                              });
                        
                this.ele.mesa  = this.mesa;
                  this.ticketService.Asignarte (this.ele).subscribe(res => {
                      const resp = res.json();
                      if (resp > 0) {
                          console.log(" ya da asignar");
                          console.log(this.ele);
                          }
                      });
                
                  this.d1= true;
                  this.d2= !this.d1; 
                  this.d3= this.d2;   
               }       
       }
         else
            {
                       this.msgs = [];
                       this.msgs.push({severity:'info',summary:'No hay mas Tickets',detail:'No Existen Clientes Esperando'});
                       
                        
            }
           });
    
    
    
  
     let interval = setInterval(() => {
            this.value = this.value +0.1;
            if( (this.value <= 100) && (this.d1) ) {
               
               this.xxx++;
               this.yyy=this.xxx;
               this.hor= Math.trunc(this.yyy/3600);
               this.yyy= Math.trunc( this.yyy%3600);
               this.min= Math.trunc(this.yyy/60);
               this.seg= Math.trunc(this.yyy%60);

               this.ti = this.hor+":"+this.min+":"+this.seg;
           
            }
            else
                {
                    clearInterval(interval);
                    this.value=0;
                }
        }, 1000);

  
 
      
       
  }






  Terminar()
  {   this.ele.tiempo = this.ti;
      this.ele.servicio = this.selected;
    
      console.log(this.ele);
      
      this.ticketService.Cerrar ( this.ele).subscribe(res => {
            const resp = res.json();
               console.log(resp );
                this.Id="";
                this.Fe="";
                this.Ho="";
                this.TT="";
                this.me="";
                this.es=null;
                this.ser="";
                this.ti="";
                this.selected="";
                // this.es=;
                this.value=102;
               
            // }
        });
        this.d1= false;
        this.d3= true;
        this.d2= !this.d1;
          this.msgs = [];
        
 
  }
  escoger(n:number) {
  this.mesa=n;
   this.display=false;
   
 }
 rellamar()
 {
  this.msgs = [];
  this.msgs.push({severity:'info',summary:'Volviendo a Llamar ',detail:'Ticket tkt'});

   this._connection = this._signalR.createConnection(this.o);
    this._connection.start().then((c) => {
      
    c.invoke('Sendllamada', this.o.qs.name, this.ele ).then(() => {

      });
    });

 }

 setdata(data: any)
 {
    // this.
    this.TT= String (data['tkt']);
    this.me= String (data['mesa']);
 }
  ngOnInit() {
   this.xxxx= false;
   this.display=true;
   // console.log(this.mesa);
   this.d1= false;
   this.d2= !this.d1;
   this.d3= true;

  }

}



